import React from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend
} from "recharts";
import {data} from '../Data';
export default function BarChartExample (){
    return(
        <BarChart
      width={500}
      height={300}
      data={data}
      margin={{
        top: 5,
        right: 30,
        left: 20,
        bottom: 5
      }}
    >
      <CartesianGrid strokeDasharray="3,0" vertical={false} />
      <XAxis dataKey="name" />
      <YAxis />
      <Tooltip />
      <Legend />
      <Bar dataKey="student" fill="green" />
      <Bar dataKey="rankedStudent" fill="orange"/>
      <Bar dataKey="fees" fill="yellow"/>
    </BarChart>
    )
}