import React from 'react';

const ProgressBar =()=>{
   return(
   <div class="container" >
       
        <div class="panel panel-default" style={{background:"#2E2E2E",color:"white"}}> 
        <h2>Account Summary</h2>
            <div class="panel-body">
                <div>230 Users (70 License Available)<br/> <br/> 
                <table width="100%">
                    <tr>
                        <td width="10%"></td>
                        <td width="80%">
                            <div class="progress">
                            <div class="progress-bar" role="progressbar" style={{width: "15%", paddingRight:2}} aria-valuenow="15" aria-valuemin="0" aria-valuemax="100"></div>
                            <span style={{marginRight:2}}></span>
                            <div class="progress-bar bg-success" role="progressbar" style={{width: "30%"}} aria-valuenow="30" aria-valuemin="0" aria-valuemax="100"></div>
                            <span style={{marginRight:2}}></span>
                            <div class="progress-bar bg-info" role="progressbar" style={{width: "20%"}} aria-valuenow="20" aria-valuemin="0" aria-valuemax="100"></div>
                          </div>
                        </td>
                        <td width="10%"></td>
                    </tr>
                </table>

                <table width="100%">
                    <tr>
                    <td width="10%"></td>
                    <td width="30%">
                       <span class="btn btn-secondary" style={{background:"#007bff"}}></span> <label  for="option1">Mobile</label> 
                   </td>
                    <td width="30%">
                    <span class="btn btn-secondary" style={{background:"#28a745"}}></span> <label  for="option1">Portal</label> 
                    </td>
                    <td width="20%">
                    <span class="btn btn-secondary" style={{background:"#17a2b8"}}></span> <label  for="option1">IW</label> 
                    </td>
                    <td width="10%"></td>
                    </tr></table>
                    <br/> <br/>
                <div>76% Storage Full (760 GB Of 1 TB)</div><br/> 
                <table width="100%">
                        <tr>
                            <td width="10%"></td>
                            <td width="80%">
                                <div class="progress">
                                <div class="progress-bar" role="progressbar" style={{width: "15%", paddingRight:2}} aria-valuenow="15" aria-valuemin="0" aria-valuemax="100"></div>
                                <span style={{marginRight:2}}></span>
                                <div class="progress-bar bg-success" role="progressbar" style={{width: "30%"}} aria-valuenow="30" aria-valuemin="0" aria-valuemax="100"></div>
                                <span style={{marginRight:2}}></span>
                                <div class="progress-bar bg-info" role="progressbar" style={{width: "20%"}} aria-valuenow="20" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                            </td>
                            <td width="10%"></td>
                        </tr>
                    </table> 
                    <table width="100%">
                    <tr>
                    <td width="5%"></td>
                    <td width="40%">
                       <span class="btn btn-secondary" style={{background:"#007bff"}}></span> <label  for="option1">Document</label> 
                   </td>
                    <td width="30%">
                    <span class="btn btn-secondary"  style={{background:"#28a745"}}></span> <label  for="option1">Charts</label> 
                    </td>
                    <td width="25%">
                    <span class="btn btn-secondary"  style={{background:"#17a2b8"}}></span> <label  for="option1">Pictures</label> 
                    </td>
                    <td width="5%"></td>
                    </tr></table>
                    <br/> <br/>
                </div>
            </div>
       </div>
       
    </div>
 )
}

export default ProgressBar;