import {
    AreaChart,
    Area,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    ResponsiveContainer,
    Legend
  } from "recharts";
  import {realData} from '../RealData';

  const renderLegend = (value, entry) => {
    return (
     <span>{value}</span>
     
    );
  }

 const TotalCollbrationChart =()=>{
    return (
        <AreaChart
          width={500}
          height={400}
          data={realData}
          margin={{
            top: 10,
            right: 30,
            left: 30,
            bottom: 0
          }}
        >
            <defs>
    <linearGradient id="rankedStudent" x1="0" y1="0" x2="0" y2="1">
      <stop offset="5%" stopColor="#8884d8" stopOpacity={1}/>
      <stop offset="95%" stopColor="#8884d8" stopOpacity={0}/>
    </linearGradient>
    <linearGradient id="student" x1="0" y1="0" x2="0" y2="1">
      <stop offset="5%" stopColor="#82ca9d" stopOpacity={.8}/>
      <stop offset="95%" stopColor="#82ca9d" stopOpacity={0}/>
    </linearGradient>
    <linearGradient id="fees1" x1="0" y1="0" x2="0" y2="1">
      <stop offset="5%" stopColor="red" stopOpacity={.30}/>
      <stop offset="95%" stopColor="red" stopOpacity={0}/>
    </linearGradient>
  </defs>
          <CartesianGrid strokeDasharray="3,0" vertical={false} />
          <XAxis dataKey="name" interval={'preserveStartEnd'}/>
          <YAxis type="number" domain={[0, 20]}  tickFormatter={(value)=> value + ' Hour'}/>
          <Legend formatter={renderLegend} />
          <Tooltip />
          <Area type="monotone" dataKey="portal" stroke="#8884d8" fillOpacity={1} fill="url(#rankedStudent)" />
          <Area type="monotone" dataKey="iw" stroke="#82ca9d" fillOpacity={1} fill="url(#student)" />
          <Area type="monotone" dataKey="mobile" stroke="red" fillOpacity={1} fill="url(#fees1)" />
           
          
            </AreaChart>
        )
  }

  export default TotalCollbrationChart;