import {EMPLOYEE_DETAILS} from '../type/index';

const initialState={
   num: 0, 
   employee: "RAHUL PUNDIR",
   employees: [],
  }

export default function employeeReducer(state = initialState , action) {
  switch (action.type) {
    case EMPLOYEE_DETAILS:
      return{
        ...state,
        //employee: action.name
        employees:action.employeeData

      }
    case "INCREMENT":
      return {
        ...state,
        num: state.num + action.step
      };
    case "DECREMENT":
      return {
        ...state,
        num: state.num - action.step
      };
    default:
      return state;
  }
}

